import * as React from "react"
import Box from "./box";
import CardFace from "./card";
import { handleFunc } from "./utils";

const CommonBox = ({ values, setValue, type, text }) => {

    const [safeHouseCis1, setSafeHouseCis1] = React.useState('cis');

    return (
        <Box styles={["box-container"]} accept={type} handleDrop={handleFunc(setValue)} state={values} text={text}>
            {
                (<span className="num_text">{values.length}张</span>)
            }
            {
                (
                    <label className="inner-checkbox">
                        <input type="checkbox"
                            value=""
                            checked={safeHouseCis1 !== 'cis'}
                            onChange={(event) => {
                                const { value, checked } = event.target;
                                if (checked) {
                                    setSafeHouseCis1('not-cis');
                                } else {
                                    setSafeHouseCis1('cis');
                                }
                            }}
                        />
                        逆
                    </label>
                )
            }
            {
                values.map(card => (
                    <CardFace
                        styles={["desk_card", "card-class-common"]}
                        key={card.id}
                        id={card.id}
                        code={card.code}
                        setOldState={setValue}
                        type={type}
                        positive={true}
                        cis={safeHouseCis1 === 'cis'} />
                ))
            }
        </Box>
    );
}

export default CommonBox;