import * as React from "react"
import { useDrag } from "react-dnd";
import './game.css';
import { PhotoView } from 'react-photo-view';

/**
 * 可以被放置的卡片
 * @param {*} id 
 * @param {*} code 
 * @param {*} setOldState 
 * @param {*} type 
 * @param {*} positive 
 * @param {*} cis 
 * @returns 
 */
const CardFace = ({ id, code, setOldState, type, positive, cis, styles, children }) => {
    const [{ isDragging }, drag] = useDrag(() => ({
        type: type,
        item: { id, code, positive, cis, setOldState },
        collect: (monitor) => ({
            isDragging: monitor.isDragging(),
        }),
    }));

    if (isDragging) {
        return (
            <div className="card-class" ref={drag}></div>
        );
    }

    // 通过cis 加载不同的class 来旋转90度
    return (
        <div className={`card-class ${cis ? '' : 'rotate-90'} ${styles && styles.join(' ')}`} >
            {/* 这里正反要改变 */}
            {positive &&
                (
                    <PhotoView src={`/cards/${code}.png`}>
                        <img
                            // className={`${cis ? '' : 'rotate-90'}`}  ${cis ? '' : 'rotate-90'}
                            ref={drag} src={`/cards/${code}.png`} />
                    </PhotoView>
                )}
            {!positive &&
                (<img
                    // className={`${cis ? '' : 'rotate-90'}`}
                    ref={drag} src='/back.png' />)}
            {children}
        </div>
    )
}

export default CardFace;