// 一会返回处理的高阶函数
const handleFunc = (setNewState) => {
    return (item, monitor, state) => {
        if (state.find((each) => each.id === item.id)) return;
        item.setOldState((prev) => {
            const index = prev.findIndex((each) => each.id === item.id);
            const copy = [...prev];
            copy.splice(index, 1);
            return copy;
        });
        setNewState((prev) => {
            return [...prev, { id: item.id, code: item.code }];
        });
    };
}

export { handleFunc };