import * as React from "react"
import './game.css'
import { useDrop } from "react-dnd";



const Box = ({ accept, handleDrop, state, text, styles, children }) => {
    const [{ isOver }, drop] = useDrop(() => ({
        accept: accept,
        collect: (monitor) => ({
            isOver: monitor.isOver(),
        }),
        drop: (item, monitor) => { handleDrop(item, monitor, state) },
    }),
        [state]
    );

    return (
        <div className={`box-class ${styles && styles.join(' ')}`} ref={drop}>
            <span className="absolute-element">{text}</span>
            {children}
        </div>
    );
};

export default Box;